<template>
	<div>
		<el-row>
			<el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
				<div class="hearder">
					<img src="../../static/images/24201905.png" alt="图片丢失"/>					
				</div>
			</el-col>
		</el-row>
		<el-row class="mincent">
			<el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
				<div style="height: 10px;"></div>
			</el-col>
			<el-col :xs="14" :sm="14" :md="14" :lg="14" :xl="14" class="headerMin">
				<div>Bright</div>
				<div>
					<router-link to="/">首页</router-link>
				</div>
				<div>
					<router-link to="/SampleReels">作品集</router-link>
				</div>
				<div>
					<router-link to="/ProfessionalSkills">职业技能</router-link>
				</div>
				<div>
					<router-link to="/AboutUs">关于自己</router-link>
				</div>
			</el-col>
			<el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
				<div style="height: 10px;"></div>
			</el-col>
		</el-row>
		<el-row>
			<!-- 路由出口 -->
  			<!-- 路由匹配到的组件将渲染在这里 -->
  			<router-view></router-view>
		</el-row>
		<div class="bottomCent">
			您是第
			<span>{{val}}</span>
			位访问该网站的用户,这么恶心的页面让您看到受惊了。哈哈
		</div>
	</div>
</template>

<script>
	export default{
		data() {
	      return {
	      	val:0
	      }
		},
		created:function(){
			var caution=false
			function setCookie(name,value,expires,path,domain,secure) 
			{
			 var curCookie=name+"="+escape(value) +
			 ((expires)?";expires="+expires.toGMTString() : "") +
			 ((path)?"; path=" + path : "") +
			 ((domain)? "; domain=" + domain : "") +
			 ((secure)?";secure" : "")
			 if(!caution||(name + "=" + escape(value)).length <= 4000)
			 {
			 document.cookie = curCookie
			 }
			 else if(confirm("Cookie exceeds 4KB and will be cut!"))
			 {
			 document.cookie = curCookie
			 }
			}
			function getCookie(name) 
			{
			 var prefix = name + "="
			 var cookieStartIndex = document.cookie.indexOf(prefix)
			 if (cookieStartIndex == -1)
			 {
			 return null
			 }    
			 var cookieEndIndex=document.cookie.indexOf(";",cookieStartIndex+prefix.length)
			 if(cookieEndIndex == -1)
			 {
			 cookieEndIndex = document.cookie.length
			 }
			 return unescape(document.cookie.substring(cookieStartIndex+prefix.length,cookieEndIndex))
			}
			function deleteCookie(name, path, domain) 
			{
			 if(getCookie(name)) 
			 {
			 document.cookie = name + "=" + 
			 ((path) ? "; path=" + path : "") +
			 ((domain) ? "; domain=" + domain : "") +
			 "; expires=Thu, 01-Jan-70 00:00:01 GMT"
			 }
			}
			function fixDate(date) 
			{
			 var base=new Date(0)
			 var skew=base.getTime()
			 if(skew>0)
			 {
			 date.setTime(date.getTime()-skew)
			 }    
			}
			var now=new Date()
			fixDate(now)
			now.setTime(now.getTime()+365 * 24 * 60 * 60 * 1000)
			var visits = getCookie("counter")
			if(!visits)
			{
			 visits=1;
			}  
			else
			{
			 visits=parseInt(visits)+1;
			}  
			setCookie("counter", visits, now)
//			document.write("您是到访的第" + visits + "位用户！")
			this.val=visits;
		},
	    methods:{
	
	    }
	}
</script>

<style>
	.hearder>img{
		width: 100%;
		height: 400px;
	}
	.mincent{
		height: 40px;
		background-color: #ccc;
	}
	.headerMin>div{
		display: inline-block;
	}
	.bottomCent{
		text-align: center;
		margin-top: 80px;
	}
	.bottomCent>span{
		color: red !important;
	}
</style>


